body{
  height: auto;
  overflow-x: hidden;
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}
h3{
  text-align: center;
  font-size: 5rem;
  margin: 0;
  font-family: 'futura';
  text-transform: uppercase;
}
span{
position: relative;
left: 53%;
top: -20px;
  font-size:1.5rem;
  margin: 0;
  font-family: 'futura';
  text-transform: uppercase;}
.app {
  display: flex;
  max-width: 1200px;
 margin: 0 auto;
  justify-content: center;
  text-align: center;
  padding: 2rem 5rem;
}

.todo_input {
  height: 16px;
  width: 700px;
  padding: 10px;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  border: 2px solid blueviolet;
}

.todo_button {
  margin-left: 12px;
  border: 2px solid blueviolet;
  background-color:#f56df5 ;
  height: 40px;
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}

.todo {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
  border: 1px solid black;
  font-weight: 700;
  padding: 2rem;
  margin-bottom: 5px;
  min-width: 340px;
}

.edit {
  width: 66px;
  font-weight: 700;
  background: blueviolet;
  border: none;
  border-top-right-radius: 5px;
  height: 33px;
  border-bottom-right-radius: 5px;
  color: white;
  font-size: medium;
}

.delete {
  width: 66px;
  font-weight: 700;
  background: white;
  border: 2px solid blueviolet;
  border-top-left-radius: 5px;
  height: 33px;
  color: blueviolet;
  border-bottom-left-radius: 5px;
  font-size: medium;
}

.form {
  padding-top: 27px;
  padding-bottom: 27px;
}

.name {
 width: 70%;
 text-transform: capitalize;
 padding: 1rem;
  text-align: left;
}

@media screen and (max-width:768px) {
  .form{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .todo_input {
    width: 100%;
}
h3 {
  font-size: 3rem;
  
}
.todo {
  
  min-width: 249px;
}
.name{
  width: auto;
}
span {
  left: 47%;
  top: -11px;
  
}
}